'use client';

import { useState, useEffect } from 'react';
import GameBoard from './components/GameBoard';
import { generateSecretNumber } from './utils/gameLogic';

export default function Home() {
  const [secretNumber, setSecretNumber] = useState('');
  const [gameOver, setGameOver] = useState(false);
  const [won, setWon] = useState(false);

  useEffect(() => {
    setSecretNumber(generateSecretNumber());
  }, []);

  const startNewGame = () => {
    setSecretNumber(generateSecretNumber());
    setGameOver(false);
    setWon(false);
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-gray-800 to-gray-900 p-4">
      <div className="bg-metal-gradient p-8 rounded-2xl shadow-[0_20px_60px_-15px_rgba(0,0,0,0.5)] w-full max-w-md border border-white/10">
        <h1 className="text-4xl font-bold text-gray-800 mb-8 text-center tracking-tight">
          Number Game
        </h1>
        <GameBoard 
          secretNumber={secretNumber}
          onGameOver={() => setGameOver(true)}
          onWin={() => setWon(true)}
        />
        {(gameOver || won) && (
          <div className="mt-6 text-center">
            <p className="text-xl mb-4 text-gray-800">
              {won ? 'Congratulations! You won! ðŸŽ‰' : 'Game Over!'}
              {gameOver && !won && <div>The number was {secretNumber}</div>}
            </p>
            <button
              onClick={startNewGame}
              className="bg-button-gradient text-white px-6 py-3 rounded-xl shadow-skeuomorphic hover:opacity-90 active:shadow-skeuomorphic-pressed transition-all transform active:translate-y-0.5"
            >
              Play Again
            </button>
          </div>
        )}
      </div>
    </main>
  );
}